<template>
  <div id="main">
        <h3>{{countAlias}}</h3>
        <button @click="add">增加</button>
        <!-- 子组件 -->
        <child :data="data"></child>
        <el-input
            placeholder="请输入身份证号码"
            v-model="credentialsCode"
            :validate-event="true"
            autocomplete="off"
            size="medium"
            @blur="change"
            style="width:250px"
            clearable>
        </el-input>
  </div>
</template>

<script>
import Vue from 'vue';
import Child from '@/components/child.vue';
import { mapState } from 'vuex';
import IdCard from '@/assets/js/IdCard.js';
Vue.prototype.validid = IdCard;
export default {
    components:{
        Child
    },
    data(){
        return{
            data: 2,
            credentialsCode: ''
        }
    },
    computed: {
        ...mapState({
            countAlias: 'count',
        })
    },
    methods:{
        add(){
            // 调用全局方法
            this.$store.commit('addCount');
        },
        change(){
            if(this.credentialsCode){
                const data = this.validid.cnid(this.credentialsCode);
                if(data){
                    this.$message({
                        message: '输入的证件号码正确!',
                        type: 'success'
                    });
                }else{
                    this.$message.error('输入的证件号码不正确，请重新输入!');
                }
            }
        }
    }
}
</script>
<style scoped lang="scss">
#main{
    width: 400px;
    height: 300px;
}
</style>